import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import styles from './login.module.css';
import Modal from './modals/Modal';
import AuthService from '../../services/AuthService';

function LoginPage() {
    let navigate = useNavigate();
    //   useNavigate   navigate  

    const [isModalOpen, setIsModalOpen] = useState(false);

    const openModal = () => {
        setIsModalOpen(true)
    };
    const closeModal = () => { setIsModalOpen(false) };

    const [isModalOpen1, setIsModalOpen1] = useState(false);
    const openModal1 = () => {
        setIsModalOpen1(true)
    };
    const closeModal1 = () => { setIsModalOpen1(false) };

    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    const passwordRegex = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}$/;

    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const [userId, setID] = useState('');
    const [username, setUsername] = useState('');

    const [verificationCode, setVerificationCode] = useState('');
    const [inputVerificationCode, setInputVerificationCode] = useState('');
    const [checkVerification, setCheckVerification] = useState(false);
    const fakeVerificationCode = "1234";

    const authService = new AuthService();

    const [IdCheck, setIdCheck] = useState(false);

    const handleUsernameChange = (e) => {
        setUsername(e.target.value);
    }
    const handleIdChange = (e) => {
        setID(e.target.value);
    }

    const handleIdCheck = () => {
        setIdCheck(true);
    }

    const handleEmailChange = (e) => {
        setEmail(e.target.value);
    };

    const handlePasswordChange = (e) => {
        setPassword(e.target.value);
    };

    const handleConfirmPasswordChange = (e) => {
        setConfirmPassword(e.target.value);
    };

    const handleVerificationCodeChange = (e) => {
        setInputVerificationCode(e.target.value);
    };

    const handleSendVerificationCode = async () => {
        //     
        //      .
        //      
        // setVerificationCode(fakeVerificationCode);
        await fetch("172.16.230.134:4000/email/send", {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({email}),
          });
        alert(" .  .");
    };

    const handleVerificationCheck = async () => {
        //    ,    
        const isCheck= await fetch("172.16.230.134:4000/email/check", {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({inputVerificationCode}),
          });
        if (isCheck) {
            setCheckVerification(true);
            alert(".");
        } else {
            setCheckVerification(false);
            alert("  .");
        }
    }

    const AccountSubmit = async (e) => {
        e.preventDefault();

        //   
        if (!emailRegex.test(email)) {
            alert("   .");
            return;
        }
        //   
        if (!passwordRegex.test(password)) {
            alert("  8 ,       .");
            return;
        }
        //   
        if (password !== confirmPassword) {
            alert("    .");
            return;
        }

        authService.signup(userId, username, password, email);
        console.log(" ");
    };

    const FindPassSubmit = async (e) => {
        e.preventDefault();
        if (!checkVerification) {
            alert("  .");
            return;
        }
        //   
        if (!emailRegex.test(email)) {
            alert("   .");
            return;
        }
        //   
        if (!passwordRegex.test(password)) {
            alert("  8 ,       .");
            return;
        }
        //   
        if (password !== confirmPassword) {
            alert("    .");
            return;
        }

        console.log("");
        authService.changepass(email, password);
    };

    const submitHandler = async (event) => {
        event.preventDefault();
        const isLoginSuccessful = await authService.login(userId, password);
        if (isLoginSuccessful) {
            navigate('/main');
        } else {
            alert(" .");
        }
    };

    return (
        <div>
            <div className={styles.ring}>
                <i style={{ '--clr': '#00ff0a' }}></i>
                <i style={{ '--clr': '#ff0057' }}></i>
                <i style={{ '--clr': '#fffd44' }}></i>
                <div className={styles.login}>
                    <h2>N E T R U N N E R</h2>
                    <form onSubmit={submitHandler}>
                        <div className={styles.inputBx}>
                            <input type="text" value={userId} onChange={handleIdChange} placeholder="" required />
                        </div>
                        <div className={styles.inputBx}>
                            <input type="password" value={password} onChange={handlePasswordChange} placeholder="" required />
                        </div>
                        <div className={styles.inputBx}>
                            <input type="submit" value="" />
                        </div>
                    </form>
                    <div className={styles.links}>
                        <a href="#" onClick={(e) => { e.preventDefault(); openModal(); }} > </a>
                        <Modal isOpen={isModalOpen} closeModal={closeModal}>
                            {
                                <div className="modalContent">
                                    <span className="close" onClick={closeModal} >&times;</span>
                                    <h2> </h2>
                                    <form onSubmit={FindPassSubmit} >
                                        <div className="inputBx">
                                            <input type="email" value={email} onChange={handleEmailChange} placeholder="" required />
                                            <input type="button" value="" onClick={handleSendVerificationCode} />
                                        </div>
                                        <div className="inputBx">
                                            <input type="text" value={inputVerificationCode} onChange={handleVerificationCodeChange} placeholder="" required />
                                            <input type="button" value=" " onClick={handleVerificationCheck} />
                                        </div>
                                        <div className="inputBx" id="newPasswordBox">
                                            <input type="password" value={password} onChange={handlePasswordChange} placeholder=" " required />
                                            <input type="password" value={confirmPassword} onChange={handleConfirmPasswordChange} placeholder="  " required />
                                            <input type="submit" value=" " id="resetPasswordButton" />
                                        </div>
                                    </form>
                                </div>
                            }
                        </Modal>
                        <a href="#" onClick={(e) => { e.preventDefault(); openModal1(); }}></a>
                        <Modal isOpen={isModalOpen1} closeModal={closeModal1}>
                            {
                                <div className="modalContent">
                                    <span className="close" onClick={closeModal1}>&times;</span>
                                    <h2></h2>
                                    <form onSubmit={AccountSubmit} >
                                        <div className="inputBx">
                                            <input type="text" value={username} onChange={handleUsernameChange} placeholder="" required />
                                        </div>
                                        <div className="inputBx">
                                            <input type="email" value={email} onChange={handleEmailChange} placeholder=" " required />
                                            <input type="button" value=" " onClick={handleSendVerificationCode} />
                                        </div>
                                        <div className="inputBx">
                                            <input type="text" value={inputVerificationCode} onChange={handleVerificationCodeChange} placeholder="" required />
                                            <input type="button" value=" " onClick={handleVerificationCheck} />
                                        </div>
                                        <div className="inputBx">
                                            <input type="text" value={userId} onChange={handleIdChange} placeholder="" required />
                                            <input type="button" onClick={handleIdCheck} value="" />
                                        </div>
                                        <div className="inputBx">
                                            <input type="password" value={password} onChange={handlePasswordChange} placeholder="" required />
                                        </div>
                                        <div className="inputBx">
                                            <input type="password" value={confirmPassword} onChange={handleConfirmPasswordChange} placeholder=" " required />
                                        </div>
                                        <div className="inputBx">
                                            <input type="submit" value="" />
                                        </div>
                                    </form>
                                </div>
                            }
                        </Modal>
                    </div>
                </div>
            </div >
        </div >
    );
}
export default LoginPage;
