class AuthService {
  constructor() {
    this.baseUrl = process.env.REACT_APP_API_URL || '172.16.230.134:4000';
  }

  async signup(userId, username, password, email) {
    try {
      const response = await fetch(`${this.baseUrl}/auth/signup`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ userId, username, password, email }),
      });

      if (!response.ok) {
        throw new Error(` : ${response.statusText}`);
      }

      console.log(' .');
    } catch (error) {
      console.error(' :', error);
    }
  }

  async login(userId, password) {
    try {
      const response = await fetch(`${this.baseUrl}/auth/signin`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ userId, password }),
      });

      if (!response.ok) {
        throw new Error(` : ${response.statusText}`);
      }

      const data = await response.json();

      if (data.accessToken) {
        localStorage.setItem('accessToken', data.accessToken);
        return true; //  
      } else {
        console.error('JWT  .');
        return false; //  
      }
    } catch (error) {
      console.error(' :', error);
      return false; //  
    }
  }

  async logout() {
    //   JWT 
    localStorage.removeItem('accessToken');
    window.location.href = '/';

    try {
      const response = await fetch(`${this.baseUrl}/auth/signout`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('accessToken')}`,
        },
      });

      if (!response.ok) {
        throw new Error(` : ${response.statusText}`);
      }

      console.log(' .');
    } catch (error) {
      console.error(' :', error);
    }
  }

  async changepass(email, password) {
    try {
      const response = await fetch(`${this.baseUrl}/auth/changepass`, {
        method: 'PATCH',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email, password }),
      });

      if (!response.ok) {
        throw new Error(`  : ${response.statusText}`);
      }

      console.log(' .');
    } catch (error) {
      console.error('  :', error);
    }
  }
}

export default AuthService;
