import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import styles from './Quest.module.css';

const Quest = ({ missionId }) => {
  const [missionData, setMissionData] = useState(null);

  useEffect(() => {
    //    
    const fetchMissionData = async () => {
      try {
        const response = await fetch(`http://172.16.230.134:4000/missions/${missionId}`);
        const data = await response.json();
        setMissionData(data);
      } catch (error) {
        console.error('   :', error);
      }
    };

    fetchMissionData();
  }, [missionId]);

  if (!missionData) {
    return <div>Loading...</div>;
  }

  const { missionID, scenario, type, correctAnswer, node, reward } = missionData;

  // node      
  const hasNode = node && node.nodeId && node.nodeMAC && node.nodeIP && node.nodePort && node.nodeDirectorys && node.nodeProgram && node.nodeFile;

  return (
    <div className={styles.questContainer}>
      <h2> </h2>
      <div className={styles.missionDetail}>
        <p><strong> ID:</strong> {missionID}</p>
        <p><strong>:</strong> {scenario}</p>
        <p><strong>:</strong> {type}</p>
        <p><strong> :</strong> {correctAnswer}</p>
        {hasNode && (
          <>
            <h3> </h3>
            <p><strong> ID:</strong> {node.nodeId}</p>
            <p><strong>MAC :</strong> {node.nodeMAC}</p>
            <p><strong>IP :</strong> {node.nodeIP}</p>
            <h4> </h4>
            <p><strong>TCP:</strong> {node.nodePort.TCP.servicePort} ({node.nodePort.TCP.state})</p>
            <p><strong>UDP:</strong> {node.nodePort.UDP.servicePort} ({node.nodePort.UDP.state})</p>
            <h4> </h4>
            <ul>
              {node.nodeDirectorys.dirPath.map((path, index) => (
                <li key={index}>{path}</li>
              ))}
            </ul>
            <h4> </h4>
            <p>{node.nodeProgram.programName}</p>
            <h4> </h4>
            <p><strong> :</strong> {node.nodeFile.File_name}</p>
            <pre>{node.nodeFile.File_content}</pre>
          </>
        )}
      </div>
      {reward && (
        <>
          <h3></h3>
          <p>{reward.point}</p>
          <p>{reward.toolFile}</p>
        </>
      )}
    </div>
  );
};

Quest.propTypes = {
  missionId: PropTypes.string.isRequired
};

export default Quest;
