import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import styles from './shop.module.css';

const Shop = ({ userId }) => {
  const [availablePoints, setAvailablePoints] = useState(0);
  const [tools, setTools] = useState([]);
  const [purchasedTools, setPurchasedTools] = useState([]);

  useEffect(() => {
    //    
    const fetchPoints = async () => {
      try {
        const response = await fetch(`http://172.16.230.134:4000/missions/points/${userId}`);
        const data = await response.json();
        setAvailablePoints(data.points);
      } catch (error) {
        console.error('  :', error);
      }
    };

    //      
    const fetchTools = async () => {
      try {
        const response = await fetch('http://172.16.230.134:4000/missions/tools');
        const data = await response.json();
        setTools(data);
      } catch (error) {
        console.error('  :', error);
      }
    };

    fetchPoints();
    fetchTools();
  }, [userId]);

  //   
  const handlePurchase = (tool) => {
    if (availablePoints >= tool.cost) {
      setAvailablePoints(availablePoints - tool.cost);
      setPurchasedTools([...purchasedTools, tool]);
    }
  };

  return (
    <div className={styles.shopContainer}>
      <div className={styles.shopHeader}>
        <h2>  </h2>
        <p> : {availablePoints}</p>
      </div>
      <div className={styles.toolsList}>
        {tools.map(tool => (
          <div key={tool.id} className={styles.toolItem}>
            <h3>{tool.name}</h3>
            <p>: {tool.cost} </p>
            <button 
              onClick={() => handlePurchase(tool)} 
              disabled={availablePoints < tool.cost || purchasedTools.includes(tool)}>
              {availablePoints >= tool.cost ? '' : ' '}
            </button>
          </div>
        ))}
      </div>
    </div>
  );
};

Shop.propTypes = {
  userId: PropTypes.string.isRequired
};

export default Shop;
